import React from "react";
import GoogleMapReact from "google-map-react"
import PropTypes from 'prop-types';
import supercluster from 'points-cluster';
import './SimpleMap.css'

class SimpleMap extends React.Component {

    static defaultProps = {
        onMarkerClick: undefined,
        center: {
            lat: 53.5,
            lng: -2.3
        },
        zoom: 12
    };

    constructor(props) {
        super(props);

        this.state = {
            clusters: []
        };
    }

    onMarkerClick(location) {
        if (undefined !== this.props.onMarkerClick) {
            this.props.onMarkerClick(location);
        }
    }

    onClusterClick(cluster) {
        if (undefined !== this.props.onMarkerClick) {
            this.props.onClusterClick(cluster);
        }
    }

    createClusters = (locations, mapOptions) => {

        var map;

        if (undefined !== mapOptions.bounds) {

            const superclusters = supercluster(locations, {
                minZoom: 0,
                maxZoom: 16,
                radius: 40,
            });

            const clusters = superclusters(mapOptions);

            map = clusters.map(({ wx, wy, numPoints, points }) => ({
                lat: wy,
                lng: wx,
                numPoints,
                id: `${numPoints}_${points[0].id}`,
                points,
            }));
        }
        else {
            map = [];
        }

        this.setState({ clusters: map });
    };

    handleMapChange = ({ center, zoom, bounds }) => {
        this.createClusters(this.props.locations, {
            center,
            zoom,
            bounds,
        });
    };

    getMostSeriousItem(items) {
        var x;
        for (x = 0; x < items.length; x++) { if ('red' === items[x].status) return items[x]; }
        for (x = 0; x < items.length; x++) { if ('amber' === items[x].status) return items[x]; }
        for (x = 0; x < items.length; x++) { if ('yellow' === items[x].status) return items[x]; }
        return items[0]; // all green
    }

    getIcon(status) {
        return `${process.env.PUBLIC_URL}/Motor-32-${status}.png`;
    }

    renderMarker(item) {
        if (1 === item.numPoints) {
            const point = item.points[0];
            return <img className='marker-icon' src={this.getIcon(point.status)} alt={point.status} lat={item.lat} lng={item.lng} onClick={() => this.onMarkerClick(point)} />;
        }
        else {
            const point = this.getMostSeriousItem(item.points);
            return <div className='marker-group' lat={item.lat} lng={item.lng} onClick={() => this.onClusterClick(item)}>
                <img className='marker-icon' src={this.getIcon(point.status)} alt={point.status} />
                <div className="marker-counter">
                    {item.points.length}
                </div>
            </div>;
        }
    }

    render() {
        return (
            // Important! Always set the container height explicitly
            <div className="main-map">
                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyBeQJKcfrMZkXf3kRJZZkD34V3rnM-IgYI" /* https://console.cloud.google.com/google/maps-apis/credentials?project=hvpdjsoft */ }}
                    defaultCenter={this.props.center}
                    defaultZoom={this.props.zoom}
                    onChange={this.handleMapChange}
                >
                    {
                        this.state.clusters.map(
                            item => { return <div className="marker-wrapper" lat={item.lat} lng={item.lng}>{this.renderMarker(item)}</div> }
                        )
                    }
                </GoogleMapReact>
            </div>
        );
    }
}

SimpleMap.propTypes = {
    locations: PropTypes.array,
    onMarkerClick: PropTypes.func,
    onClusterClick: PropTypes.func,
    zoom: PropTypes.number,
    center: PropTypes.object
};

export default SimpleMap;